import GabaritObject from "jx/editor/editionitems/GabaritObject";
export default class TextStyle extends GabaritObject {
    constructor(dataObject) {
        dataObject.data.fontsList = dataObject.data.fontsList || "fonts";
        dataObject.gabarit = [
            {
                "id": "fontFamily",
                "type": dataObject.data.fontsList == "fonts" ? "FontSelector" : "ComboBox",
                "description": "",
                "editable": true,
                "value": "Harimau",
                "options": dataObject.data.fontsList
            },
            {
                "id": "color",
                "type": "Color",
                "editable": true,
                "description": "",
                "value": "#ffffff"
            },
            {
                "id": "bold",
                "type": "Boolean",
                "description": "",
                "visible": false,
                "editable": true,
                "value": false
            },
            {
                "id": "italic",
                "type": "Boolean",
                "description": "",
                "visible": false,
                "editable": true,
                "value": false
            },
            {
                "id": "fontSize",
                "type": "Number",
                "min": 1,
                "max": 200,
                "description": "",
                "editable": true,
                "value": "20"
            },
            {
                "id": "textAlign",
                "type": "ComboBox",
                "description": "",
                "editable": true,
                "value": "center",
                "options": [
                    "left",
                    "center",
                    "right",
                    "justify"
                ]
            },
            {
                "id": "overflow",
                "type": "ComboBox",
                "description": `
 				      Défini le dépassement de texte de la zone prévue.
 				      auto: affiche automatiquement la barre de défilement verticale si nécessaire seulement.
 				      visible: le texte dépasse en bas sans se soucier de la limite.
 				      hidden: la partie qui dépasse est cachée.
 				      scroll: affiche toujours les barres de défilement, nécessaire ou non.
 				      `,
                "editable": true,
                "value": "auto",
                "options": [
                    "auto",
                    "visible",
                    "hidden",
                    "scroll"
                ]
            }
        ];
        super(dataObject);
    }
}
